.todoList {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 542px;
  padding: 0;
  margin: -55px 0 0;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0px 25px 20px 15px rgb(0 0 0 / 9%);
}

.caption {
  margin: 50px 0 0;
  font-family: JosefinSans;
  font-size: 14px;
  font-style: normal;
  line-height: 17px;
  font-weight: 400;
  color: hsl(234, 11%, 52%);
  word-spacing: -1px;
}

.filterByDateButton {
  margin: 10px 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.filterByDateButton:hover {
  color: hsl(234, 39%, 85%);
}

@media screen and (max-width: 600px) {
  .todoList {
    max-width: calc(100% - 48px);
    width: 100%;
    margin: -28px 0 0;
  }

  .caption {
    margin-top: 42px;
  }
}
